.message {
    margin-top: 47px;
}

.imageAndText {
    display: flex;
    justify-content: flex-end;
    color: #FFFFFF;
    align-items: flex-end;
}

.imageAndText img {
    order: 2;
    width: 48px;
    height: 48px;
}

.text {
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03);
    background: #0066CC;
    /*box-shadow:0px 1px 2px rgba(29, 33, 38, 0.1);*/
    margin-right: 10px;
    position: relative;
    border-radius: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}

.text:after {
    content: '';
    position: absolute;
    right: -10px;
    bottom: 0px;
    border: 10px solid transparent;
    border-bottom: 10px solid #0066CC;

}

.name {
    text-align: end;
    margin: 1px 25px 3px 13px;
    font-size: 18px;
}

.messageText {
    margin: 1px 25px 3px 13px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}

.time {
    text-align: end;
    font-weight: 600;
    font-size: 10px;
}