.button {
    border-radius: 3px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px 24px;
    gap: 10px;
    cursor: pointer;
    background-color: #0066CC;
    border: none;
    color: #FFFFFF;

}

.disabled {
   opacity: 0.5;
    color: #002E5C;
}

.secondary {
    color:  #0066CC;
    background-color: #fff;
    border: 1px solid #06c;
}

.default {
    background-color: #0066CC;
}

.red {
    background-color: #CC1439;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B3;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B31232;
}

.button:disabled {
    cursor: not-allowed;
}
